import React, { FC, useState } from 'react';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Avatar,
  Chip,
  Divider,
  Grid,
  makeStyles,
  Typography,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import BankIcon from './BankIcon';
import { Money } from '../shared/Money';
import { MoneyText } from '../components/MoneyText';
import { BucketResponse } from '../buckets/BucketsRepository';

//todo: move
interface AccountResponse {
  id: string;
  name: string;
  bank: string;
  amount: Money;
  buckets: BucketResponse[];
}

interface AccountsListProps {
  accounts: AccountResponse[];
}

const useStyles = makeStyles(() => ({
  root: {
    width: '96%',
    marginLeft: '2%',
    marginRight: '2%',
    marginTop: '2%',
    marginBottom: '4%',
  },
}));

export const AccountsList: React.FC<AccountsListProps> = ({ accounts }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = useState('');

  return (
    <div className={classes.root}>
      {accounts.map(account => (
        <Account
          key={account.id}
          account={account}
          expanded={expanded === account.id}
          onExpand={id => setExpanded(id)}
          onHideDetails={() => setExpanded('')}
        />
      ))}
    </div>
  );
};

interface AccountProps {
  account: AccountResponse;
  expanded: boolean;
  onExpand: (id: string) => void;
  onHideDetails: (id: string) => void;
}

const Account: FC<AccountProps> = ({
  account: { id, name, bank, amount, buckets },
  expanded,
  onExpand,
  onHideDetails,
}) => {
  const onChange = (expand: boolean) => {
    if (expand) {
      onExpand(id);
    } else {
      onHideDetails(id);
    }
  };

  return (
    <Accordion expanded={expanded} onChange={(_, expand) => onChange(expand)}>
      <AccordionSummary expandIcon={<ExpandMoreIcon />}>
        <Grid container direction="column">
          <Grid container direction="row" spacing={2}>
            <Grid alignContent={'flex-start'} item xs={2}>
              <BankIcon bank={bank} />
            </Grid>
            <Grid alignContent={'flex-start'} item xs={10} zeroMinWidth>
              <Typography variant="subtitle1" noWrap>
                {name}
              </Typography>
            </Grid>
          </Grid>

          <Grid container direction="row">
            <Grid item xs>
              <Typography variant="subtitle2" color="textSecondary">
                <MoneyText value={amount} />
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </AccordionSummary>
      <AccordionDetails>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography variant={'h6'}>Kupki</Typography>
            <Divider />
          </Grid>
          {buckets.map(b => (
            <Grid item>
              <Chip avatar={<Avatar>{b.icon}</Avatar>} label={b.name} />
            </Grid>
          ))}
        </Grid>
      </AccordionDetails>
    </Accordion>
  );
};
