export const MbankIcon: FC = () => {
  return (
    <SvgIcon titleAccess="MBank">
      <rect width="30%" height="100%" x="0" style={{ fill: '#E90809' }} />
      <rect width="3%" height="100%" x="30%" style={{ fill: '#000' }} />
      <rect width="30%" height="100%" x="33%" style={{ fill: '#DA9909' }} />
      <rect width="3%" height="100%" x="63%" style={{ fill: '#000' }} />
      <rect width="30%" height="100%" x="66%" style={{ fill: '#20AB49' }} />
      <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" fill="#fff">
        m
      </text>
    </SvgIcon>
  );
};

interface BankIconProps {
  bank: string;
}

const BankIcon: FC<BankIconProps> = ({ bank, ...props }) => {
  switch (bank) {
    case 'mbank':
      return <MbankIcon {...props} />;
    case 'pekao':
      return <PekaoIcon {...props} />;
    default:
      return (
        <Avatar variant="square" alt={bank} title={bank}>
          {bank.substring(0, 1)}
        </Avatar>
      );
  }
};

export default BankIcon;
