import React from 'react';
import { Money } from '../shared/Money';

const Locale = 'pl-PL';

interface MoneyProps {
  value: Money;
}

export const MoneyText: React.FC<MoneyProps> = ({ value: { value, currency } }) => {
  return (
    <React.Fragment>
      {value.toLocaleString(Locale, { style: 'currency', currency: currency, useGrouping: true })}
    </React.Fragment>
  );
};
